<template>
  <div id="acccountDetail">
    <van-overlay :show="load" :z-index="10000" class="loadBox" :lock-scroll="true">
      <van-loading class="load" type="spinner" color="#1989fa" :vertical="true" v-if="load" />
    </van-overlay>
    <div class="content">
      <header>
        <div class="balance">
          我的余额 ￥
          <span v-if="!totalLoad">{{balance}}</span>
          <van-loading type="spinner" color="#c9c9c9" size="18" v-else />
        </div>
        <van-dropdown-menu :overlay="false">
          <van-dropdown-item
            v-model="tradeSort"
            :options="tradeSortList"
            :disabled="disableDropdown"
            @change="changeSort"
          />
        </van-dropdown-menu>
      </header>
      <van-pull-refresh
        v-model="pullLoading"
        :success-text="successMessage"
        @refresh="pullRefresh"
        :disabled="disablePullRefresh"
      >
        <van-list
          v-model="pushLoading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
          :immediate-check="false"
          :error.sync="pushError"
          error-text="请求失败，点击重新加载"
        >
          <div class="incomeExpendList">
            <div v-for="(item,index) in dataArray " :key="index" class="incomeExpend">
              <div class="info">
                <p
                  class="tradeSort"
                >{{item.order_type=="user_income"?"分销收入":item.order_type=="user_withdraw"?"提现":''}}</p>
                <div class="tradeInfo">
                  <p class="tradeTime">{{item.paid_at|submitTimeFormatType}}</p>
                  <!-- <p class="tradeWay">支付宝</p> -->
                </div>
              </div>
              <p class="amounMoney">
                {{item.order_type=="user_income"?"+":item.order_type=="user_withdraw"?"-":''}}
                <span>{{item.amount}}</span>元
              </p>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>
<script>
import {
  getAccountDetailData,
  getWithDrawalData,
} from "../assets/javascript/apis.js";
export default {
  data() {
    return {
      tradeSort: 0,
      getDataFlag: true, //是否能发起请求
      pullLoading: false, //控制下拉刷新状态
      pushLoading: false, //控制上拉加载状态
      finished: false, //控制上拉加载是否启用
      disableDropdown: false, //控制下拉菜单是否启用
      disablePullRefresh: false, //控制下拉刷新是否禁用
      tradeSortList: [
        { text: "全部", value: 0, type: "all" },
        { text: "提现记录", value: 1, type: "out" },
        { text: "分销收入", value: 2, type: "income" },
      ],
      dataArray: [],
      balance: 0,
      page: 1,
      successMessage: "",
      pushError: false,
      load: true,
      totalLoad: true,
    };
  },
  created() {
    this.load = true;
    this.getData();
    //设置分享链接
    this.$shareConfig();
  },
  methods: {
    //当交易类别变化时执行的函数
    changeSort(val) {
      console.log(this.tradeSortList[val].type);
      this.getData(this.tradeSortList[val].type);
    },
    //获取页面数据
    getData(type, page, refreshData = true) {
      //限制发起请求频率
      if (!this.getDataFlag) return;
      this.getDataFlag = false;
      //限制发起请求时，下拉选择框的使用
      this.disableDropdown = true;
      //禁用下拉刷新
      this.disablePullRefresh = true;
      //生成参数
      const data = {
        type: type ? type : "all",
        page: page ? page : "",
      };
      // console.log(data);
      if (refreshData) {
        this.totalLoad = true;
      }
      //发起请求
      getAccountDetailData(data)
        .then((res) => {
          console.log(res);

          //处理数据
          if (refreshData) {
            this.dataArray = [];
          }

          // 18658807005

          this.dataArray.push(...res.orders.data);
          this.balance = res.balance;
          this.page = res.orders.current_page;

          console.log(this.dataArray);
          //转换可以发起请求状态
          this.getDataFlag = true;
          //转换下拉菜单可以使用状态
          this.disableDropdown = false;
          if (refreshData) {
            //展现下拉刷新成功提示信息
            this.successMessage = "刷新成功";
            //下拉刷新页面后，更改状态
            this.pullLoading = false;
            //重新启用下拉刷新
            this.disablePullRefresh = false;
            //隐藏掉加载图标
            this.totalLoad = false;
          } else {
            //上拉加载更多
            this.pushLoading = false;
          }
          //当加载到最后一页，禁用上拉加载更多，同时解决了上拉加载更多不停调用的问题
          if (res.orders.current_page >= res.orders.last_page) {
            this.finished = true;
          }
          this.load = false;
        })
        .catch((res) => {
          //转换可以发起请求状态
          this.getDataFlag = true;
          //转换下拉菜单可以使用状态
          this.disableDropdown = false;
          //隐藏掉加载图标
          this.totalLoad = false;
          this.load = false;
          if (refreshData) {
            //展现下拉刷新成功提示信息
            this.successMessage = "刷新失败，请重新刷新";
            //下拉刷新页面后，更改状态
            this.pullLoading = false;
            //重新启用下拉刷新
            this.disablePullRefresh = false;
          } else {
            //加载数据失败后，修改上拉加载更多的状态
            this.pushLoading = false;
            //当请求失败时，展现上拉加载更多失败信息
            this.pushError = true;
          }
        });
    },
    //下拉刷新页面
    pullRefresh() {
      //首先置空数据源数组，接着重新获取数据，
      this.getData(this.tradeSortList[this.tradeSort].type);
    },
    //上拉加载更多
    onLoad() {
      // console.log(123);
      this.page = this.page + 1;
      // debugger;
      this.getData(this.tradeSortList[this.tradeSort].type, this.page, false);
    },
  },
};
</script>
<style lang="scss" scoped>
#acccountDetail {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .loadBox {
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    header {
      width: 100%;
      height: 35px;
      padding: 0 20px;
      box-sizing: border-box;
      background: rgba(242, 244, 246, 1);
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1000;
      .balance {
        display: flex;
        justify-content: center;
        font-size: 12px;
        color: rgba(170, 170, 170, 1);
      }
      /deep/.van-dropdown-menu {
        position: relative;
        .van-dropdown-menu__bar {
          width: 80px;
          height: 25px;
          padding: 0 20px;
          background: rgba(255, 255, 255, 1);
          border-radius: 25px;
          .van-dropdown-menu__item {
            .van-dropdown-menu__title {
              margin-right: 15px;
              font-size: 12px;
              color: rgba(7, 7, 7, 1);
            }
          }
        }
        .van-dropdown-item {
          width: 120px;
          height: 150px;
          background: rgba(255, 255, 255, 1);
          box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.24);
          border-radius: 6px;
          position: absolute;
          right: 0;
          top: 100%;
          .van-dropdown-item__content {
            top: 10%;
            .van-dropdown-item__option {
              line-height: 40px;
              padding: 0 15px;
              .van-cell__title {
                text-align: center;
              }
              .van-cell__value {
                display: none;
              }
            }
          }
        }
      }
    }
    /deep/.van-pull-refresh {
      margin-top: 35px;
      flex-grow: 1;
      overflow: visible;
      display: flex;
      flex-direction: column;
      .van-pull-refresh__track {
        flex-grow: 1;
        height: auto;
        display: flex;
        flex-direction: column;
        .van-pull-refresh__head {
        }
      }
    }
    /deep/.van-list {
      flex-grow: 1;
    }
    .incomeExpendList {
      flex-grow: 1;
      width: 100%;
      padding: 0 0 0 20px;
      box-sizing: border-box;
      .incomeExpend {
        height: 65px;
        padding: 0 20px 0 0;
        background: white;
        border-bottom: 1px solid rgba(242, 244, 246, 1);
        display: flex;
        align-items: center;
        justify-content: space-between;
        .info {
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-evenly;
          .tradeSort {
            font-size: 14px;
            color: rgba(7, 7, 7, 1);
          }
          .tradeInfo {
            line-height: 10px;
            display: flex;
            font-size: 12px;
            color: rgba(170, 170, 170, 1);
            .tradeWay {
              margin-left: 20px;
            }
          }
        }
        .amounMoney {
          font-size: 18px;
          font-family: DIN;
          font-weight: 500;
          color: rgba(7, 7, 7, 1);
        }
      }
    }
  }
}
</style>